<template>
  <div class="wrapper">
      <div class="zz" v-show="flag">
      </div>
       <div class="darg" v-hulr>
      <i class="iconfont icon-xinfeng"></i>
    </div>
  </div>
</template>

<script>
export default {
  name: "",
  data() {
    return {
      flag:false
    };
  },
  created() {},
  methods: {
  },
  directives: {
    hulr(el,inserted,vnode) {
      console.log(vnode.context.flag);
      var maxW = document.documentElement.clientWidth;
      var maxH = document.documentElement.clientHeight;
      var startX = 0;
      var startY = 0;
      el.addEventListener("touchstart", function(e) {
        startX = e.targetTouches[0].pageX - this.offsetLeft;
        startY = e.targetTouches[0].pageY - this.offsetTop;
        vnode.context.flag=true
      },false);
      el.addEventListener("touchmove", function(e) {
        var leftX = e.targetTouches[0].pageX - startX-25;
        var topY = e.targetTouches[0].pageY - startY-25;
        var thisW = e.targetTouches[0].target.clientWidth;
        var parentW = e.targetTouches[0].target.offsetParent.clientWidth;
        var thisH = e.targetTouches[0].target.clientHeight;
        var parentH = e.targetTouches[0].target.offsetParent.clientHeight;
        this.style.left = leftX + "px";
        this.style.top = topY + "px";
        
      },false);
      el.addEventListener("touchend", function(e) {
        vnode.context.flag=false
      },false);
    },
  },
};
</script>

<style lang="scss" scoped>
.wrapper{
  width: 100vw;
  height: 100vh;
  .zz{
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0);
  position: fixed;
  top: 0;
  left: 0;
  }
}
.darg {
  width: 50px;
  height: 50px;
  position: absolute;
  bottom: 100px;
  right: 20px;
  background: blue;
  color: white;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  i {
    font-size: 26px;
  }
}
</style>
